.panel{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #282c34;
}
.link {
    text-decoration: none;
    color: white;
    font-size: large;
}
.link:active{
    color: #61dafb;
}